*,
*:before,
*:after {
  box-sizing: border-box;
}

/* Regular */

@font-face {
  font-family: 'League Gothic';
  src: url("../fonts/leaguegothic-regular-webfont.eot");
  src: url("../fonts/leaguegothic-regular-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/leaguegothic-regular-webfont.woff") format("woff"), url("../fonts/leaguegothic-regular-webfont.svg#league_gothic_italicregular") format("svg");
  font-weight: 400;
  font-style: normal;
}

body {
  margin: 0 auto;
  background-color: #ffffff;
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-style: normal;
}

.seconadary-color {
  /* change letter-color to light-blue in text*/
  color: #22aaec;
}

.header {
  display: block;
  width: 100%;
  border-bottom: 3px solid #22aaec;
}

.header__navigation {
  display: block;
}

.header__name {
  display: block;
  width: 100%;
  margin: 0 auto;
  padding-top: 9px;
  color: #076e9f;
  font-family: "League Gothic", sans-serif;
  font-size: 32px;
  text-align: center;
  text-transform: uppercase;
}

.header__social-links {
  display: block;
  position: absolute;
  margin: 0;
  top: 16px;
  right: 30px;
}

.header__link-container {
  display: inline-block;
  padding: 3px 0;
  border: 1px solid transparent;
}

.header__link-container.-hover {
  border-radius: 0 0 8px 8px;
}

.header__link-container.-hover:hover {
  transition: 0.7s;
  border-top: 1px solid #22d5ff;
  background-color: rgba(36, 0, 0, 0.79);
}

.header__link-container a {
  display: block;
  background: url("../assets/headerLinks.png");
  width: 18px;
  height: 18px;
}

.header__link-container.-evernote {
  background-position: 0 0;
}

.header__link-container.-twitter {
  background-position: -23px 0;
}

.header__link-container.-pinterest {
  background-position: -46px 0;
}

.header__link-container.-linkedin {
  background-position: -69px 0;
}

.header__link-container.-facebook {
  background-position: -92px 0;
}

.header__link-container.-delicious {
  background-position: -115px 0;
}

.header__navigation-container {
  display: block;
  width: 100%;
  margin: 0 auto;
  padding-top: 18px;
  padding-bottom: 12px;
  text-align: center;
}

.header__navigation-link {
  display: inline-block;
  border: 1px solid transparent;
  margin-right: 30px;
  padding: 5px 16px 4px;
}

.header__navigation-link.-active {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
  border: 1px solid #045279;
  background: #076e9f linear-gradient(to top, rgba(0, 0, 0, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
}

.header__navigation-link.-active a {
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.55);
  color: #ffffff;
}

.header__navigation-link a {
  color: #707070;
  text-decoration: none;
  text-transform: uppercase;
}

.header__navigation-link:hover {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
  border: 1px solid #045279;
  background: #076e9f linear-gradient(to top, rgba(0, 0, 0, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
}

.header__navigation-link:hover a {
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.55);
  color: #ffffff;
  transition: 0.7s;
}

.header__burger {
  display: none;
  position: absolute;
  left: 20px;
  top: 15px;
  width: 24px;
  height: 24px;
  cursor: pointer;
  color: transparent;
  font-size: 0;
  background: url("../assets/menu.png") 100%, no-repeat;
  z-index: 1;
}

.header__checkbox {
  display: none;
}

.header__checkbox:checked + .header__burger {
  background-image: url("../assets/close.png");
}

.header__checkbox:checked ~ .header__navigation-container {
  display: block;
}

@media all and (max-width: 599px) {
  .header__name {
    padding-bottom: 40px;
  }

  .header__navigation-link {
    display: block;
    width: 120px;
    transition: 1s;
    margin: 0 auto;
  }

  .header__navigation-link.-active {
    background: transparent;
    border: 1px solid transparent;
  }

  .header__navigation-link.-active a {
    color: #707070;
    text-shadow: none;
  }

  .header__navigation-link.-active:hover {
    border: 1px solid #045279;
    background: #076e9f linear-gradient(to top, rgba(0, 0, 0, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
  }

  .header__navigation-link.-active:hover a {
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.55);
    color: #ffffff;
    transition: 0.7s;
  }

  .header__burger {
    display: block;
    top: 55px;
  }

  .header__navigation-container {
    display: none;
  }

  .header__social-links {
    top: 56px;
  }
}

.main-banner {
  display: block;
  width: 100%;
  background: url("../assets/main-bg.png") 100%, no-repeat;
  background-size: cover;
  padding-top: 93px;
  margin: 0 auto;
}

.main-banner__visit {
