  background-color: rgba(255, 255, 255, 0.8);
  padding: 7px 0 6px;
  margin: 0 auto;
}

.main-banner__comments-container {
  height: 122px !important;
  padding-top: 26px;
  padding-bottom: 26px;
  background-color: rgba(0, 0, 0, 0.5);
}

.main-banner__slider-container {
  display: block;
  width: 25%;
}

.main-banner__slider-item {
  display: block;
  width: 190px !important;
  height: 69px;
  margin: 0 auto;
  border-radius: 35px 0 0 34px;
