}

.main-banner__p-s {
  display: block;
  color: #828282;
  font-size: 9px;
  text-align: left;
  padding-left: 86px;
}

@media all and (max-width: 1170px) {
  .main-banner__slider-container {
    width: 33%;
  }
}

@media all and (max-width: 715px) {
  .main-banner__slider-container {
    width: 50%;
  }
}

@media all and (max-width: 477px) {
  .main-banner__slider-container {
    width: 100%;
  }
}

.left-bar {
  display: flex;
  float: left;
  max-width: 250px;
  flex-direction: column;
  padding-left: 36px;
}

.left-bar__item {
  display: block;
  max-width: 196px;
  border-right: 1px dotted #d5d5d5;
  border-bottom: 1px dotted #d5d5d5;
}

.left-bar__item:nth-child(2) {
