  text-transform: uppercase;
}

.left-bar__item-banner.-first-banner {
  margin: 32px 0 19px 0;
}

.left-bar__item-banner.-second-banner {
  margin: 17px 0 16px 0;
}

.left-bar__item-banner.-third-banner {
  margin-top: 22px;
  margin-bottom: 12px;
}

.left-bar__post-container {
  float: left;
  width: 100%;
  margin-bottom: 11px;
}

.left-bar__post-container img {
  display: inline-block;
  float: left;
  clear: left;
