}

.left-bar__post-container p {
  display: block;
  color: #5c5c5c;
  font-family: "Arial - Bold", sans-serif;
  font-size: 10px;
  font-weight: 400;
  text-transform: uppercase;
  margin: 0;
}

.left-bar__post-container a {
  text-decoration: none;
  margin-top: -25px;
  color: #5c5c5c;
  font-family: "Arial", sans-serif;
  font-style: italic;
  font-size: 10px;
  font-weight: 400;
  padding-left: 6px;
}

.left-bar__post-container a:hover {
  color: #22aaec;
}

.left-bar__post-container a:before {
  content: url("../assets/logo-coments.svg");
  position: relative;
  top: 6px;
  right: 6px;
}

.left-bar__table {
  border: 1px solid white;
  margin-bottom: 45px;
}

.left-bar__table-row {
  color: #076e9f;
  font-family: "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  background-color: rgba(144, 212, 245, 0.5);
}

.left-bar__table-row:first-child {
  color: #ffffff;
  font-family: "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  background-color: #076e9f;
}

.left-bar__table-row td {
  width: 38px;
  height: 19px;
  text-align: center;
}

@media all and (max-width: 1015px) {
  .left-bar {
    max-width: 100%;
    flex-direction: row;
    justify-content: center;
    padding-left: 0;
    margin-top: -10px;
    border-bottom: 1px dotted #d5d5d5;
  }

  .left-bar__item {
    margin-left: 30px;
    border-bottom: none;
  }

  .left-bar__item .-first-banner {
    margin-top: 17px;
  }

  .left-bar__item:last-child {
    border-right: none;
  }
}

@media (max-width: 680px) {
  .left-bar {
    flex-direction: column;
    align-items: center;
  }

  .left-bar__item {
    min-width: 250px;
    border: none;
  }

  .left-bar__item:nth-child(2) {
    border: 1px dotted #d5d5d5;
    border-left: none;
    border-right: none;
  }
}

.container {
  display: block;
  max-width: 986px;
  margin: 0 auto;
}

.main-theme {
  display: block;
  max-width: 754px;
  position: relative;
  background-color: white;
  float: left;
  top: -11px;
  padding-bottom: 24px;
  border-top: 3px solid #22aaec;
  border-bottom: 1px dotted #d5d5d5;
  /*_________why-come END__________*/
}

.main-theme__why-come {
  display: block;
  width: 754px;
  min-height: 196px;
  border: 1px dotted #d5d5d5;
  border-left: none;
}

.main-theme__banner {
  color: #076e9f;
  font-family: "League Gothic", sans-serif;
  font-size: 32px;
  text-transform: uppercase;
  margin: 23px 0 13px 14px;
}

.main-theme__publication {
  display: block;
  width: 116px;
  margin-right: 27px;
  margin-left: 14px;
