  padding-top: 18px;
  margin: 0 auto;
}

.main-theme__circle:nth-child(2) {
  background: url("../assets/John.png") 100% 10%, no-repeat, #22aaec;
  background-size: cover;
  margin-left: 7px;
}

.main-theme__circle:nth-child(2) p {
  white-space: pre;
}

.main-theme__text {
  display: block;
  max-width: 546px;
  color: #5c5c5c;
  font-family: "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
  float: left;
  margin-top: 0;
}

.main-theme__read-more {
  display: inline-block;
  box-shadow: inset 0 -2px 0 rgba(35, 4, 4, 0.95);
  border: 1px solid #045279;
  background-color: #076e9f;
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.2) 0%, rgba(255, 255, 255, 0.2) 100%);
  text-decoration: none;
  color: #ffffff;
  font-family: "Arial - Bold", sans-serif;
  font-size: 10px;
  font-weight: 400;
  padding: 7px 11px;
  margin-top: 18px;
  margin-left: 157px;
}

.main-theme__read-more:hover {
  background-color: #499f5f;
  transition: 0.25s;
  margin-top: 15px;
}

.main-theme__navigation {
  display: inline-block;
  position: relative;
  width: 180px;
  height: 27px;
  float: right;
  background-color: #90d4f5;
  padding-top: 4px;
  margin-top: 20px;
  margin-right: -6px;
  z-index: 2;
}

.main-theme__navigation a {
  text-decoration: none;
  color: #076e9f;
  font-family: "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  padding-left: 37px;
}

.main-theme__navigation a:hover {
  color: white;
}

.main-theme__navigation a:first-child::before {
  content: url("../assets/like-white.png");
  position: absolute;
  left: 10px;
}

.main-theme__navigation a:last-child::before {
  content: url("../assets/logo-coments-white.png");
  position: absolute;
  margin-left: -25px;
}

.main-theme__triangle {
  display: block;
  position: relative;
  width: 0;
  height: 0;
  border-top: 6px solid #076e9f;
  border-right: 6px solid transparent;
  float: right;
  right: -180px;
  z-index: 1;
  bottom: -46px;
}

.main-theme__articles-container {
  display: flex;
  flex-wrap: wrap;
  max-width: 754px;
  min-height: 215px;
  padding-top: 18px;
}

.main-theme__article {
  display: block;
  position: relative;
  z-index: 0;
  max-width: 363px;
  min-height: 217px;
  background: #eaeaea;
  border-radius: 10px 0 0 0;
  margin-left: 13px;
  padding-top: 26px;
}

.main-theme__article:before,
.main-theme__article:after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 15px;
  left: 10px;
  width: 47%;
  height: 5%;
  top: 89%;
  max-width: 300px;
  background: rgba(0, 0, 0, 0.7);
  -webkit-box-shadow: 0 15px 10px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: 0 15px 10px rgba(0, 0, 0, 0.7);
  box-shadow: 0 15px 10px rgba(0, 0, 0, 0.7);
  -webkit-transform: rotate(-2deg);
  -moz-transform: rotate(-2deg);
  -o-transform: rotate(-2deg);
  -ms-transform: rotate(-2deg);
  transform: rotate(-2deg);
}

.main-theme__article:after {
  -webkit-transform: rotate(2deg);
  -moz-transform: rotate(2deg);
  -o-transform: rotate(2deg);
  -ms-transform: rotate(2deg);
  transform: rotate(2deg);
  right: 10px;
  left: auto;
}

.main-theme__article-topic {
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.75);
  color: #076e9f;
  font-family: "League Gothic", sans-serif;
  font-size: 22px;
  font-weight: 400;
  text-transform: uppercase;
  padding-left: 30px;
  margin: 0;
}

.main-theme__article-topic.-second-article {
  max-width: 245px;
  padding-right: 0;
}

.main-theme__posted-by {
  color: #878787;
  font-family: "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  padding-bottom: 34px;
  padding-left: 30px;
  margin: 0;
}

.main-theme__posted-by.-second-name {
  padding-bottom: 14px;
}

.main-theme__posted-by span {
  color: #076e9f;
}

.main-theme__post {
  color: #5c5c5c;
  font-family: "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 18px;
  white-space: pre;
  padding-left: 30px;
  padding-right: 77px;
  margin: 0;
}

.main-theme__post.-bottom {
  padding-bottom: 24px;
}

.main-theme__post a {
  text-decoration: none;
  color: #076e9f;
}

.main-theme__navigation-article {
  display: block;
  position: relative;
  width: 100%;
  background-color: #f4f3f3;
  padding-top: 4px;
  padding-bottom: 6px;
}

.main-theme__navigation-article a {
  text-decoration: none;
  color: #076e9f;
  font-family: "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  padding-left: 30px;
}

.main-theme__navigation-article a:hover {
  color: #2af14b;
  transition: 0.7s;
}

.main-theme__navigation-article a:first-child {
  padding-left: 255px;
}

.main-theme__navigation-article a:first-child::before {
  content: url("../assets/like-blue.png");
  position: relative;
  left: -3px;
  top: 2px;
}

.main-theme__navigation-article a:last-child::before {
  content: url("../assets/logo-coments-blue.png");
  position: relative;
  left: -4px;
  top: 5px;
}

.main-theme__ad-box {
  display: block;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 81px;
  background-color: #f4f3f3;
  background-image: linear-gradient(to top, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.05) 100%);
  margin: 20px 2px 0 13px;
}

.main-theme__ad-box a {
  display: block;
  text-decoration: none;
  text-align: center;
