  .container {
    display: flex;
    flex-direction: column;
  }

  .main-theme {
    order: -1;
    margin: 0 auto;
  }
}

@media all and (max-width: 780px) {
  .main-theme__why-come {
    width: 90%;
    min-height: 270px;
    margin: 0 auto;
  }

  .main-theme__text {
    float: none;
    clear: left;
    padding: 15px 0 0 15px;
  }

  .main-theme__read-more {
    margin: 21px 0 10px 15px;
  }

  .main-theme__articles-container {
    justify-content: center;
  }

  .main-theme__article:first-child {
    margin-bottom: 20px;
  }

  .main-theme__ad-box {
    width: 90%;
  }
}

@media (max-width: 460px) {
  .main-theme__banner {
    font-size: 26px;
  }
}

@media (max-width: 360px) {
  .main-theme {
    max-width: 320px;
  }

  .main-theme__text {
    padding-right: 10px;
  }

  .main-theme__article {
    margin: 0 5px;
  }

  .main-theme__post {
    white-space: normal;
  }
}

/*media for IE*/

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .for-ie {
    padding-right: 21px;
  }
}

@media all and (-ms-high-contrast: none) and (max-width: 1015px) {
  .main-theme {
    order: -1;
    margin-left: calc(50% - 380px);
  }
}

@media all and (-ms-high-contrast: none) and (max-width: 780px) {
  .main-theme {
    margin: 0 auto;
  }
}

.footer {
  display: block;
  max-width: 959px;
  margin: 0 auto;
}

.footer .header__name.-footer {
  display: block;
  float: left;
  width: 100px;
  font-size: 13px;
  text-align: left;
  margin: 17px 0 15px 7px;
  padding-top: 0;
  padding-bottom: 0;
}

.footer__menu {
  display: block;
  float: right;
}

.footer__menu-container {
  display: inline-block;
}

.footer__menu-container a {
  text-decoration: none;
  color: #707070;
  font-family: "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  text-transform: uppercase;
}

.footer__menu-container a:hover {
  transition: 0.7s;
  color: #07adfa;
}

.footer__rights {
  display: block;
  width: 100%;
  text-align: center;
  border-top: 1px dotted #d5d5d5;
  color: #787878;
  font-family: "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  text-transform: uppercase;
  float: left;
  margin: 0;
  padding-top: 13px;
  padding-bottom: 10px;
}

@media (max-width: 680px) {
  .footer__menu {
    display: block;
    clear: both;
    float: none;
    text-align: center;
    padding-left: 0;
  }

  .footer .header__name.-footer {
    width: 100%;
    text-align: center;
    margin-left: 0;
  }
}

/*slick-slider-stylesheets*/

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  border-bottom: 3px solid rgb(34, 170, 236);
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  width: 190px;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
  /*width: 190px;     !*custom style*!*/
  /*margin: 0 25px;   !*custom style*!*/
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

@charset "UTF-8";

/* Slider */

.slick-loading .slick-list {
  background: #fff;
}

/* Icons */

@font-face {
  font-family: 'slick';
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/slick-fonts/slick.eot");
  src: url("../fonts/slick-fonts/slick.eot?#iefix") format('embedded-opentype'), url("../fonts/slick-fonts/slick.woff") format('woff'), url("../fonts/slick-fonts/slick.ttf") format('truetype'), url("../fonts/slick-fonts/slick.svg#slick") format('svg');
}

/* Arrows */

.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: transparent;
  outline: none;
  background: transparent;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: .25;
}

.slick-prev:before,
.slick-next:before {
  font-family: 'slick';
  font-size: 20px;
  line-height: 1;
  opacity: .75;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir='rtl'] .slick-prev {
  right: -25px;
  left: auto;
}

.slick-prev:before {
  content: '←';
}

[dir='rtl'] .slick-prev:before {
  content: '→';
}

.slick-next {
  right: -25px;
}

[dir='rtl'] .slick-next {
  right: auto;
  left: -25px;
}

.slick-next:before {
  content: '→';
}

[dir='rtl'] .slick-next:before {
  content: '←';
}

/* Dots */

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  font-family: 'slick';
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: '•';
  text-align: center;
  opacity: .25;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  opacity: .75;
  color: black;
}